<script>
	import Button from "@smui/button/bare.js";
	import "@smui/button/bare.css";
	import Slider from "@smui/slider/bare.js";
	import "@smui/slider/bare.css";
	import FormField from  "@smui/form-field/bare.js";
	import   "@smui/formfield/bare.css";
	

	import Paper, { Title, Subtitle, Content } from "@smui/paper/bare.js";
	import "@smui/paper/bare.css";

	import Fraction from "./Fraction.svelte";

	let prior = 0.3;
	let sensitivity = 70;
	let specifity = 99.5;
	let persons = 10000;
	$: true_positive = (prior / 100) * (sensitivity / 100);
	$: false_positive = (1 - prior / 100) * (1 - specifity / 100);
	$: true_negative = (1 - prior / 100) * (specifity / 100);
	$: false_negative = (prior / 100) * (1 - sensitivity / 100);
</script>	

<main>
	<h1>Baeyes</h1>
	<section>
		<div style="text-align: left;">

			<pre class="status">Smitteprocent: {prior} %</pre>
		
			<Slider bind:value={prior} min={0} max={25} step={1} discrete />
		  </div>

		  <div style="text-align: left;">
		
			<pre class="status">Sensitivitet (sande positive vs. falske negative): {sensitivity} %</pre>
		
			<Slider bind:value={sensitivity} min={0} max={100} step={1} discrete />
		  </div>
		  <div style="text-align: left;">

			<pre class="status">Specifitet (sande negative vs. falske positive): {specifity} %</pre>
	
		
			<Slider bind:value={specifity} min={0} max={100} step={1} discrete />
		  </div>
		  <div style="text-align: left;">
	
			<pre class="status">Antal testede personer: {persons}</pre>

		
			<Slider bind:value={persons} min={0} max={1000000} step={1000} discrete />
		  </div>


</section>
		<div style="width: 100%; overflow: hidden;">
			<div style="width: 600px; float: left;">
				<div class="paper-container">
					<Paper class="paper-demo">
						<Title style="text-align: center;">Positiv test</Title>
						<subtitle
							>Sandsynlighed for at en person, der er testet
							positiv i virkeligheden er smittet (sand positiv)</subtitle
						>
						<Content>
							<Fraction
								functionname=""
								numerator="Sande postive"
								denominator="Sande positive + falske positive"
								result="{(
									(true_positive /
										(true_positive + false_positive)) *
									100
								).toFixed(2)}  %"
							/>

							<Fraction
								functionname=""
								numerator="{prior}% * {sensitivity}%"
								denominator="({prior}% * {sensitivity}% ) + ( {(
									100 - specifity
								).toFixed(1)} % * {100 - prior} %)"
								result={(
									(true_positive /
										(true_positive + false_positive)) *
									100
								).toFixed(2) + " %"}
							/>

							<Fraction
								functionname=""
								numerator={true_positive.toFixed(5)}
								denominator="{true_positive.toFixed(
									4
								)} + {false_positive.toFixed(5)}"
								result={(
									(true_positive /
										(true_positive + false_positive)) *
									100
								).toFixed(2) + "%"}
							/>
							<Fraction
							functionname=""
							numerator="{(true_positive.toFixed(5)*persons).toFixed(1)} personer"
							denominator="{(true_positive.toFixed(
								4
							)*persons).toFixed(1)} personer + {(false_positive.toFixed(5)*persons).toFixed(1)} personer"
							result={(
								(true_positive /
									(true_positive + false_positive)) *
								100
							).toFixed(2) + "%"}
						/>
						</Content>
					</Paper>
				</div>
				<div class="paper-container">
					<Paper class="paper-demo">
						<Title style="text-align: center;"
							>En positiv test i tal</Title
						>
						<Content
							>Med et smittetryk på {prior} % og en population på {persons}
							personer, findes
							{((prior / 100) * persons).toFixed(0)} smittede personer
							og {persons * (1 - prior / 100)} raske personer.
							<br />
							Med en test med sensivitet på {sensitivity} %, vil testen
							korrekt identificere
							{((sensitivity / 100) * (prior / 100) * persons).toFixed(1)} personer
							som syge (sande positive)

							<br />
							<br />

							Med en specifitet på {specifity} % vil testen vil derudover fejlagtigt identificere ca. {(
								persons *
								(1 - prior / 100) *
								(1 - specifity / 100)
							).toFixed(0)} personer som positive ({((1 - specifity / 100)*100).toFixed(1)}% af {persons * (1 - prior / 100)} raske personer)  selvom personerne
							ikke er smittede (falske positive). Personer som skal
							isoleres eller forhindres i rejser m.m. på baggrund af
							en falsk positiv test.</Content
						>
					</Paper>
				</div>
			</div>

			<div style="margin-left: 640px;">
				<div class="paper-container">
					<Paper class="paper-demo">
						<Title style="text-align: center;">Negativ test</Title>
						<subtitle
							>Sandsynlighed for at en person, der er testet
							negativ i virkeligheden ikke er smittet (sand negativ)</subtitle
						>
						<Content>
							<Fraction
								functionname=""
								numerator="Sande negative"
								denominator="Sande negative + falske negative"
								result="{(
									(true_negative/
										(true_negative + false_negative)) *
									100
								).toFixed(2)}  %"
							/>
							<Fraction
								functionname=""
								numerator="{(1-prior/100)*100}% * {specifity}%"
								denominator="({(1-prior/100)*100}% * {specifity}% ) + ({prior}% * {((1-(sensitivity/100))*100).toFixed(1)}%)"
								result="{(
									(true_negative/
										(true_negative + false_negative)) *
									100
								).toFixed(2)}  %"
							/>

							<Fraction
								functionname=""
								numerator={true_negative.toFixed(5)}
								denominator="{true_negative.toFixed(
									4
								)} + {false_negative.toFixed(5)}"
								result="{(
									(true_negative/
										(true_negative + false_negative)) *
									100
								).toFixed(2)}  %"
							/>
							<Fraction
							functionname=""
							numerator="{true_negative.toFixed(4)*persons} personer"
							denominator="{true_negative.toFixed(
								4
							)*persons} personer + {false_negative.toFixed(4)*persons} personer"
							result="{(
								(true_negative/
									(true_negative + false_negative)) *
								100
							).toFixed(2)}  %"
						/>
						</Content>
					</Paper>
				</div>
				<div class="paper-container">
					<Paper class="paper-demo">
						<Title style="text-align: center;"
							>En negativ test i tal</Title
						>
						<Content>
							<p>
								Med en smitteprocent på {prior} % og en population på {persons}
								personer, findes
								{((prior / 100) * persons).toFixed(0)} smittede personer
								og {persons * (1 - prior / 100)} raske personer.<br />
								
								Med en specifitet på {specifity}% vil testen korrekt identificere {((specifity/100)*( persons * (1 - prior / 100))).toFixed(0)} personer som raske (sande negative)
								<br />
								Testen med en sensitivitet på {sensitivity} % vil fejlagtigt identficere {(
									(1 - sensitivity / 100) *
									((prior / 100) * persons)
								).toFixed(0)} personer som raske ({100-sensitivity} % af {((prior / 100) * persons).toFixed(0)} personer), men som i virkeligheden
								er syge (falske negative). Personerne bliver ikke isoleret
								og kan videregive smitte.
								<br />
							</p></Content
						>
					</Paper>
				</div>
			</div>
		</div>

</main>

<style>
	main {
		text-align: center;
		padding: 1em;
		max-width: 240px;
		margin: 0 auto;
	}

	h1 {
		color: #bd765e;
		text-transform: uppercase;
		font-size: 4em;
		font-weight: 100;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}

	.paper-container {
		background-color: #f8f8f8;
		padding: 36px 18px;
		text-align: left;
		max-width: 500px;
	}
	* :global(.paper-demo) {
		margin: 0 auto;
		max-width: 500px;
	}
</style>
